<template>
  <v-row justify="center">
    <v-dialog v-model="account" dark scrollable persistent max-width="600">
      <v-card>
        <v-card-title
          class="text-h6 grey--text text--lighten-1 font-weight-light"
        >
          Account to Link
        </v-card-title>

        <v-divider></v-divider>
        <v-card-text class="text-left">
          To link your Alexa with iwg-vpn skill please use the user and password shown below.
          <v-text-field class="mt-4"
            v-model="user"
            label="user"
            readonly
            :append-icon="'mdi-content-copy'"
            @click:append="copyUser"
          ></v-text-field>
          <v-text-field
            v-model="password"
            flat
            hide-details="auto"
            label="password"
            readonly
            :append-icon="'mdi-content-copy'"
            @click:append="copyPassword"
          ></v-text-field>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text @click="close()"> OK </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
import mixins from "../mixins";
import { mapState } from "vuex";
import backend from "@/api/backend";

export default {
  name: "confirmation",
  mixins: [mixins],
  data() {
    return {
      user: null,
      password: null,
    };
  },
  props: {
    source: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  watch: {
    account: function () {
      if (this.account) {
        this.fetchAccount();
      }
    },
  },
  computed: {
    ...mapState({
      account: (state) => state.account,
    }),
  },

  methods: {
    close() {
      this.$store.commit("closeAccountDialog");
    },

    async fetchAccount() {
    //   console.log("fetching account");

      if (this.user && this.password) {
          return;
      }  

      this.$store.commit("setBusy", true);
      let response = await backend.accountToLink();
      this.$store.commit("setBusy", false);
      this.user = response.user;
      this.password = response.password;
    },

    copyUser() {
      navigator.clipboard.writeText(this.user);
    },
    copyPassword() {
      navigator.clipboard.writeText(this.password);
    },
  },
};
</script>
