import backendEndpoints from "./endpoints.js";

async function apicall(url, options) {
    options = options || {};
    options.headers = options.headers || {};
    options.method = options.method || 'GET';
    options.headers['Content-Type'] = options.headers['Content-Type'] || 'application/json';

    console.log(`making an API call to ${url} with options: ${JSON.stringify(options)}`);

    try {
        const response = await fetch(url, options);

        if (response.status < 300) {
            return response.json();
        } else {
            console.log(`error making api call. response code ` + response.status || response.ok);
            return new Error()
        }
    } catch (e) {
        console.log(`error making api call: ` + e);
        return e;
    }
}

// const pause = ms => new Promise(resolve => setTimeout(resolve, ms))

export default {
    async accountToLink() {
        return await apicall(`${backendEndpoints.adapter}/accounts`, { method: 'POST', body: '' });
    },

    async postEndpoints(payload) {
        return await apicall(`${backendEndpoints.adapter}/endpoints`, { method: 'POST', body: JSON.stringify(payload) });
    },

    async getEndpoints() {
        return await apicall(`${backendEndpoints.adapter}/endpoints`, {});
    },

    async getCapabilities() {
        return await apicall(`${backendEndpoints.adapter}/capabilities`, {});
    },

    async getStates() {
        // await pause(1500);
        return await apicall(`${backendEndpoints.adapter}/states`, {});
    }
}