import Vue from 'vue'
import Vuex from 'vuex'
import backend from '../api/backend'

Vue.use(Vuex)

// initial state
const state = () => ({
    capabilities: [],
    browseStates: false,
    confirmation: false,
    account: false,
    busy: false
})

// getters
const getters = {}

// actions
const actions = {
    async getCapabilities({ commit, state }) {
        if (state.capabilities.length > 0) {
            return;
        }
        const respond = await backend.getCapabilities()
        commit('setCapabilities', respond.capabilities)
    },
}

// mutations
const mutations = {
    setService(state, service) {
        state.service = service
    },

    setEndpoints(state, endpoints) {
        state.endpoints.length = 0;
        state.endpoints = [...endpoints]
    },

    setCapabilities(state, capabilities) {
        state.capabilities.length = 0;
        state.capabilities = [...capabilities]
    },

    setStates(state, states) {
        state.states.length = 0;
        state.states = [...states]
    },

    openBrowseStateDialog(state) {
        state.browseStates = true
    },

    closeBrowseStateDialog(state) {
        state.browseStates = false
    },

    openConfirmationDialog(state) {
        state.confirmation = true
    },

    closeConfirmationDialog(state) {
        state.confirmation = false
    },

    openAccountDialog(state) {
        state.account = true
    },

    closeAccountDialog(state) {
        state.account = false
    },

    setBusy(state, value) {
        state.busy = value;
    }
}

export default new Vuex.Store({
    state,
    getters,
    actions,
    mutations
})